import React from 'react';
import styles from './dropdown.module.css';


interface IDropdownProps {
  button: React.ReactNode;
  children: React.ReactNode;
  isOpen ?: boolean;
  onOpen ?: ()=>void;
  onClose ?: ()=>void;
  node ?: HTMLDivElement|HTMLLIElement|null;
}

const NOOP = ()=>{};

export function Dropdown({button, children, isOpen, onOpen=NOOP, onClose=NOOP, node=null}: IDropdownProps) {

  const [isDropdownOpen, setIsDropdownOpen] = React.useState(isOpen);

  React.useEffect(()=> setIsDropdownOpen(isOpen),[isOpen]);
  React.useEffect(()=>isDropdownOpen? onOpen(): onClose(),[isDropdownOpen,onClose,onOpen]);

  const handleOpen = () => {
    if (isOpen === undefined){
      setIsDropdownOpen(!isDropdownOpen)
    }
  }

  return (  
    <div className={styles.container}>
      <div onClick={handleOpen}>
        {button}
      </div>
      {isDropdownOpen && (
        <div className={styles.listContainer}>
          <div className={styles.list} onClick={()=> setIsDropdownOpen(false)}>
            {children}
          </div>
        </div>
      )}
    </div>

  );
}
